export default {
	props:{
		getUserInfo: {
			type: [Boolean],
			default: function() {
				return false;
			}
		}
	},
	data() {
		return {
			// getUserInfo:false，
			usersetcop:true,
			index:0,
			userinfo:{},
			company_list:[],
			company_name:[]
		};
	},
	methods: {
		async wxGetUserInfo() {
			let This = this;
			uni.showModal({
				title: '微信登录',
				content: '商城需要获取你的用户信息',
				success: function(res) {
					if (res.confirm) {
						This.getuserin()
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		async formSubmit(e){
			console.log(e)
			uni.showLoading({
				title: '正在登录'
			});
			let data = e.detail.value;
			// try{
				let res =await this.$api.login(data);
				console.log(res)
				if(res.code ===200) {
					uni.setStorage({key:'token',data: res.dataMap.token});
					uni.navigateTo({
						url:'/pages/index/index'
					})
					// this.$store.dispatch('change', res.data);
					// this.$emit('ming',true);
					uni.hideLoading();
				}else{
					uni.hideLoading();
					uni.showToast({
						title: res.message,
						mask: false,
						icon:'none',
						duration: 1500
					});
				}
			// }catch(e){
			// 	uni.hideLoading();
			// 	uni.showToast({
			// 		title: '请求出错',
			// 		mask: false,
			// 		icon:'none',
			// 		duration: 1500
			// 	});
			// 	//TODO handle the exception
			// }
		},
		getuserin() {
			let This = this;
			//更新登陆状态
			uni.showLoading({
				title: '正在登录'
			});
			try {
				uni.getUserInfo({
					provider: 'weixin',
					success: function(infoRes) {
						// console.log(infoRes)
						// let nickName = infoRes.userInfo.nickName; //昵称
						// let avatarUrl = infoRes.userInfo.avatarUrl; //头像
						// This.userData['nickName'] = nickName;
						// This.userData['avatarUrl'] = avatarUrl;
						// This.getUserInfo = true;
						// uni.setStorageSync('userInfo', infoRes.userInfo);
						This.$store.dispatch('change', infoRes.userInfo);
						This.$emit('ming',true)
						uni.hideLoading();
						try {
							uni.setStorageSync('isCanUse', false); //记录是否第一次授权  false:表示不是第一次授权
							_this.updateUserInfo();
						} catch (e) {}
					},
					fail(res) {}
				});
			} catch (e) {}
		}
	
	}
}
